  //Loading ing
  componentDidMount(){

    window.scrollTo({'top':0});

    this.itemLoad();

    window.addEventListener('scroll', this.debounce(this.scrollHandler));

  }

  itemLoad(){
      let grid = document.querySelector('.item_wrap');

    // imagesLoaded state 
    // imagesLoaded this  this _this  
    let _this = this;

      // imagesLoaded / Detect when images have been loaded.
      imagesLoaded(grid, function(){
  
          //     
          _this.setState({
              isLoaded: true
          });
  
          let msnry = new Masonry( grid, {
              itemSelector: '.item_card',
              percentPosition: true,
              columnWidth: '.grid-sizer',
              gutter: '.grid-gutter'
          });
      });
  }

  

  debounce(func, wait = 10, immediate = true) {
    var timeout;
    return function() {
      var context = this, args = arguments;
      var later = function() {
        timeout = null;
        if (!immediate) func.apply(context, args);
      };
      var callNow = immediate && !timeout;
      clearTimeout(timeout);
      timeout = setTimeout(later, wait);
      if (callNow) func.apply(context, args);
    };
  };

  scrollHandler(){
    let bodyHeight = document.querySelector('body').offsetHeight;
    let winHeight = window.innerHeight;
    let winScroll = window.scrollY;
    let grid = document.querySelector('.item_wrap');

    if(winHeight + winScroll >= bodyHeight && this.state.loadItem < this.items.length) {
        this.setState({
          loadItem: this.state.loadItem + this.state.moreload
        })
        // console.log('add load!!!', this.state.loadItem, this.items.length);

        this.setState({
            isLoaded: false
        });

        this.itemLoad();
        
    }
  }

