  randomText(){

    const randomEl = document.querySelectorAll('.random_text');

    Array.prototype.forEach.call(randomEl, (el, i) => {
        let theLetters = "abcdefghijklmnopqrstuvwxyz#%&^+=-"; //customize letters cycle 
        let ctnt = el.getAttribute('data-text');
        let speed = 10; // ms per frame
        let increment = 10; // frames per step. Must be >2

        let clen = ctnt.length;
        let si = 0;
        let stri = 0;
        let block = "";
        let fixed = "";

        //Call self x times, whole function wrapped in setTimeout
        (function rustle(i) {
          setTimeout(function () {
            if (--i) { rustle(i); }
            nextFrame(i); 
            si = si + 1;
          }, speed);
        })(clen * increment + 1);

        const nextFrame = (pos) =>{
          for (var i = 0; i < clen - stri; i++) {
            //Random number
            var num = Math.floor(theLetters.length * Math.random());
            //Get random letter
            var letter = theLetters.charAt(num);
            block = block + letter;
          }
          if (si == (increment - 1)) {
            stri++;
          }
          if (si == increment) {
            // Add a letter; 
            // every speed*10 ms
            fixed = fixed + ctnt.charAt(stri - 1);
            si = 0;
          }
          el.innerHTML = fixed + block;
          block = "";
        }
    });

  }

